import React from 'react';
import styles from "./Login.module.css"
import {Navigate, NavLink} from 'react-router-dom';
import {Field, reduxForm} from "redux-form";
import {useDispatch, useSelector} from "react-redux";
import {LoginSystem} from "../../../redux/AuthReducer";
import {maxLengthCreator, required} from "../../../utils/validators/validators";
import {Input} from "../../common/FormsControl/FormsControl";

const maxLength50 = maxLengthCreator(50);

const LoginReduxForm = reduxForm({form: "login"})(function LoginForm(props) {
    return <form onSubmit={props.handleSubmit}>
        <div className={styles.LoginContainer}>
            <div className={styles.InputContainer}>
                <div>
                    {props.error && <div className={styles.formSummaryError}>
                        {props.error}
                    </div>}
                </div>
                <div>
                    <Field component={Input} name={"email"} className={styles.LoginInput} type={"text"}
                           placeholder={"E-mail"}
                           validate={[required]}
                    />
                </div>

                <div>
                    <Field component={Input} name={"password"} className={styles.LoginInput} type={"password"}
                           placeholder={"Password"}
                           validate={[required]}/>
                </div>
                <div className={styles.checkBoxContainer}>
                    <Field component={"input"} name={"rememberMe"} className={styles.checkBoxInput} type={"checkbox"}/>
                    Remember me
                </div>
                <div>
                    <button>
                        Log in
                    </button>
                </div>
                <div className={styles.AnAccount}>Do not have an account? <NavLink to={"/register"}>Sign in</NavLink>
                </div>
            </div>
        </div>
    </form>
});

function Login() {
    const [isAuth, userId] = useSelector(state => [state.AuthPage.isAuth, state.AuthPage.userId])

    const dispatch = useDispatch();

    function handleSubmit(values) {
        dispatch(LoginSystem(values.email, values.password, values.rememberMe));
    };

    if (isAuth) return <Navigate to={`/user/${userId}`}/>

    return <div className={styles.LoginBlock}>
        <div className={styles.logIn}>Sign in</div>
        <LoginReduxForm onSubmit={handleSubmit}/>
    </div>
};

export default Login;